<template>
  <div>
    <v-tabs v-model="tabs" grow>
      <v-tab :key="n" v-for="n in 2" style="width: auto" @change="changeTab(n)">
        {{ tabsTitle[n - 1] }}
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tabs">
      <v-tab-item>
        <v-card style="margin: 1%">
          <v-card-title>
            场景相关相似个股
          </v-card-title>
          <v-data-table :headers="headers" :items="stocks" :footer-props="footers" no-data-text="暂无数据">
            <template v-slot:item.headers1="{ item }" >
              <v-dialog transition="dialog-top-transition" max-width="600">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn text v-bind="attrs" v-on="on" class="ml-n5" left>
                    {{ item.headers1 }}
                  </v-btn>
                </template>
                <template v-slot:default="dialog">
                  <v-card>
                    <v-toolbar color="primary" dark
                    >{{ item.headers1 }} - {{ item.headers0 }}-个股画像</v-toolbar
                    >
                    <v-card-text>
                      <v-row
                        class="ma-2"
                        align="center"
                        justify="space-around"
                      >
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('about', item)"
                        >
                          综合评价与区间预测
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('histExp', item)"
                        >
                          股票自身历史经验
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('basicInfo', item)"
                        >
                          场景相关基础信息
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('compInfo', item)"
                        >
                          场景因子比较信息
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('simiExp', item)"
                        >
                          其他个股相似经验
                        </v-btn>
                      </v-row>
                    </v-card-text>
                  </v-card>
                </template>
              </v-dialog>

            </template>
            <template v-slot:item.headers2="{ item }">
              {{(item.headers2*100).toFixed(4)}}%

            </template>
          </v-data-table>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card style="margin: 1%">
          <v-card-title>
            场景无关相似个股
          </v-card-title>
          <v-data-table :headers="headers" :items="stocks_none" :footer-props="footers"
                        style="align-items: center" no-data-text="暂无数据">
            <template v-slot:item.headers1="{ item }">
              <v-dialog transition="dialog-top-transition" max-width="600">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn text v-bind="attrs" v-on="on" class="ml-n5" left>
                    {{ item.headers1 }}
                  </v-btn>
                </template>
                <template v-slot:default="dialog">
                  <v-card>
                    <v-toolbar color="primary" dark
                    >{{ item.headers1 }} - {{ item.headers0 }}-个股画像</v-toolbar
                    >
                    <v-card-text>
                      <v-row
                        class="ma-2"
                        align="center"
                        justify="space-around"
                      >
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('about', item)"
                        >
                          综合评价与区间预测
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('histExp', item)"
                        >
                          股票自身历史经验
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('basicInfo', item)"
                        >
                          场景相关基础信息
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('compInfo', item)"
                        >
                          场景因子比较信息
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="ma-2"
                          @click="changeItem('simiExp', item)"
                        >
                          其他个股相似经验
                        </v-btn>
                      </v-row>
                    </v-card-text>

                  </v-card>
                </template>
              </v-dialog>

            </template>
            <template v-slot:item.headers2="{ item }">
              {{(item.headers2*100).toFixed(4)}}%
            </template>
          </v-data-table>
        </v-card>
      </v-tab-item>
    </v-tabs-items>

  </div>
</template>
<script>
import {notify} from "@/components/notification";
import {getRelated, getUnRelated} from "@/api/similarity _api";
import store from "@/store";
import router from "@/router";
import {cleanMeta, initHeaders2, reMapData} from "@/utils";

export default {
  data() {
    return {
      tabs: null,
      id:"",
      tabsTitle: ['场景相关相似个股', '场景无关相似个股'],
      dialog: false,//弹窗控制
      headers:[],
      footers: {
        showCurrentPage: true,
        itemsPerPageText: '每页记录数',
        showFirstLastPage: true
      },
      stocks:[],
      stocks_none:[]
    }
  },
  methods:{
    initRelatedStock(){
      let id = this.$store.state.item.stock_id
      let time = this.$store.state.selected;
      getRelated(id, time).then(
        res =>{
          if(res.data.status===200){
            let headers = res.data.headers;
            headers = cleanMeta(headers);
            let data = res.data.data;
            initHeaders2(headers,this);
            reMapData(data,headers);
            this.stocks=data;
            notify("success", "数据更新成功");
          }else {
            notify("error", "相似度数据暂不存在");
          }
        }
      ).catch();
    },
    initUnRelatedStock(){
      let id = this.$store.state.item.stock_id
      let time = this.$store.state.selected;
      getUnRelated(id, time).then(
          res =>{
            if(res.data.status===200){
              let headers = res.data.headers;
              headers = cleanMeta(headers);
              let data = res.data.data;
              initHeaders2(headers,this);
              reMapData(data,headers);
              this.$set(this,"stocks_none",data);
              // this.stocks=data;
              notify("success", "数据更新成功");
            }else {
              notify("error", "相似度数据暂不存在");
            }
          }
      ).catch();
    },
    changeItem(goto, item) {
      let stock = item.headers1;
      item.stock_id=item["股票代码"];
      store.commit("setTitle2", "个股画像" + "——" + stock);
      store.commit("setItem", item);
      store.commit("setSelected", this.selected);
      if (goto) {
        router.push(goto);
      }
    },
    changeTab(tab){
      switch (tab) {
        case 1:
          this.initRelatedStock();
          break;
        case 2:
          this.initUnRelatedStock();
          break;
      }
    },
  },
  created() {
    this.initRelatedStock();
  }
}
</script>
